<template><section><html><head></head><body><h1>ArcGisMapServerImageryProvider</h1>
<p>The <code v-pre="">vc-provider-imagery-arcgis-mapserver</code> component is used to load tiled imagery of the ArcGIS MapServer.</p>
<h2>Examples</h2>
<h3>Load an imagerylayer with ArcGisMapServerImageryProvider</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
       <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
        <vc-provider-imagery-arcgis-mapserver :url="url" :maximum-level="maximumLevel"></vc-provider-imagery-arcgis-mapserver>
       </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>switch url</span>
        <md-select v-model="url" placeholder="switch url">
          <md-option v-for="item in options" :key="item.value" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
</doc-preview>
