<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>The URL of the ArcGIS MapServer service.</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>The ArcGIS token used to authenticate with the ArcGIS MapServer service.</td>
</tr>
<tr>
<td>tileDiscardPolicy</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The policy that determines if a tile is invalid and should be discarded.</td>
</tr>
<tr>
<td>usePreCachedTilesIfAvailable</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>If true, the server's pre-cached tiles are used if they are available. If false, any pre-cached tiles are ignored and the 'export' service is used.</td>
</tr>
<tr>
<td>layers</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>A comma-separated list of the layers to show, or undefined if all layers should be shown.</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>If true, ArcGisMapServerImageryProvider#pickFeatures will invoke the Identify service on the MapServer and return the features included in the response. If false, ArcGisMapServerImageryProvider#pickFeatures will immediately return undefined (indicating no pickable features) without communicating with the server. Set this property to false if you don't want this provider's features to be pickable. Can be overridden by setting the ArcGisMapServerImageryProvider#enablePickFeatures property on the object.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The rectangle of the layer. This rectangle can limit the visible portion of the imagery provider. <strong>structure: { west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The tiling scheme to use to divide the world into tiles. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The ellipsoid. If the tilingScheme is specified and used, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code>The width of each tile in pixels. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code>The height of each tile in pixels. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>The maximum tile level to request, or undefined if there is no maximum. This parameter is ignored when accessing a tiled server.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ArcGisMapServerImageryProvider.html">ArcGisMapServerImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the imagery provider encounters an asynchronous error.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Triggers when the provider is ready for use.</td>
</tr>
</tbody>
</table>
